const uni = require('uni').default;



































import pageHead from '../../components/page-head.vue'
import {login} from '../../common/request.js'
import utils from '../../common/utils.js'

export default{
	data(){
		return{
			title:'登录页',
			showPassword:true,
			username:'',
			upaswrd:'',
			loading: true,
			checked:true,
			haslogin:false,
			positionTop: 0
		}
	},
	onLoad:function(){
		this.initPosition();
	},
	onShow:function(){
		// console.log('login page show');
		var key = 'cc',
			data = '';
		if (key.length === 0) {
			uni.showToast('读取数据失败 key 不能为空');
		} else {
			uni.getStorage({
				key: key,
				success: (res) => {
					console.log(res.data[0].suLoginName);
					this.username = res.data[0].suLoginName;
					this.upaswrd = res.data[0].suPassword;
					this.haslogin = true;
				},
				fail: () => {
					console.log('读取数据失败 找不到 key 对应的数据');
				}
			})
		}
	},
	methods:{
		changeCh:function(){
			this.checked = !this.checked;
		},
		bindInput:function(e){
				this.username = e.target.value;
		},
		bindInput2:function(e){
				this.upaswrd = e.target.value;
		},
		changePassword:function(e){
			this.showPassword = ! this.showPassword;
		},
		tologin:function(){
			if (this.haslogin) {
				uni.redirectTo({
					url:'../swiper/swiper?mc=login success'
				});
			} else{
				var dataJson = {
					suLoginName: this.username,
					suPassword: this.upaswrd,
					suUserType: 'A'
				}
				var mycheked = this.checked;
				login(dataJson,function(res){
					if(res != null ){
						// console.log(JSON.stringify(res));
						var key = 'cc'
						var data = res.data
						if (key.length === 0) {
							uni.showModal({
								title: '保存数据失败',
								content: 'key 不能为空',
								showCancel:false
							})
						} else {
							if (mycheked) {
								uni.setStorage({
									key: key,
									data: data,
									success: (res) => {
										uni.navigateTo({
											url:'../main/main?mc=login success'
										});
									},
									fail: () => {
										uni.showToast('储存数据失败');
									}
								})
							}
						}
					}
				});
			}
		},
		initPosition() {
			/**
			* 使用 absolute 定位，并且设置 bottom 值进行定位。软键盘弹出时，底部会因为窗口变化而被顶上来。
			* 反向使用 top 进行定位，可以避免此问题。
			*/
			this.positionTop = uni.getSystemInfoSync().windowHeight - 100;
		}
	},
	components:{
		pageHead
	}
}



